<template>
  <div class="drugContainer">
    <div class="topOption">
      <el-button
        type="primary"
        plain
        style="width: 100px; height: 35px; font-size: 10px; text-align: center"
        @click="toAddDrug"
        >+</el-button
      >
      <el-button
        type="danger"
        plain
        style="width: 100px; height: 35px; font-size: 10px; text-align: center"
        ></el-button
      >
      <el-input
        placeholder=""
        prefix-icon="el-icon-search"
        style="width: 280px; margin-left: 100px"
        v-model="drugsName"
        type="text"
      >
      </el-input>
      <el-button
        type="success"
        style="width: 65px; font-size: 10px; text-align: center"
        ></el-button
      >
    </div>
    <div class="drugTable">
      <el-table
        :data="drugData.records"
        stripe
        border
        @selection-change="handleSelectionChange"
        style="width: 100%; color: black"
      >
        <el-table-column
          fixed="left"
          align="center"
          label=""
          width="80"
        >
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small"
              ></el-button
            >
          </template>
        </el-table-column>
        <el-table-column align="center" prop="drugsId" label="" width="50">
        </el-table-column>
        <el-table-column
          align="center"
          prop="drugsName"
          label=""
          width="140"
        >
        </el-table-column>
        <el-table-column
          align="center"
          prop="drugsSpecifications"
          label=""
          width="70"
        >
        </el-table-column>
        <el-table-column align="center" prop="unit" label="" width="50">
        </el-table-column>
        <el-table-column
          align="center"
          prop="drugsManufactor"
          label=""
          width="180"
        >
        </el-table-column>
        <el-table-column
          align="center"
          prop="salePrice"
          label="()"
          width="110"
        >
        </el-table-column>

        <el-table-column align="center" label="" width="320px">
          <template slot-scope="scope">
            <el-button size="mini" @click="toUpdatePage(scope.row.drugsId)"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              ></el-button
            >
            <el-button
              type="primary"
              size="mini"
              @click="changeStatus(scope.row)"
              v-if="scope.row.saleStatus == 0"
              ></el-button
            >
            <el-button
              type="warning"
              size="mini"
              @click="changeStatus(scope.row)"
              v-else="scope.row.saleStatus == 1"
              ></el-button
            >
            <el-button
              class="buttonstyle"
              type="success"
              size="mini"
              v-if="scope.row.checkStatus == 3"
              @click="toCheck(scope.row.drugsId)"
              ></el-button
            >
            <el-button
              class="buttonstyle"
              type="info"
              size="mini"
              v-if="scope.row.checkStatus == 2"
              ></el-button
            >
            <el-button
              class="buttonstyle"
              type="warning"
              size="mini"
              v-if="scope.row.checkStatus == 1"
              ></el-button
            >
            <el-button
              class="buttonstyle"
              type="success"
              size="mini"
              v-if="scope.row.checkStatus == 0"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <!-- <span class="demonstration"></span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="page"
          :page-size="pageSize"
          layout="prev, pager, next, jumper"
          :total="drugData.total"
        >
        </el-pagination>
      </div>
    </div>
    <DrugDialog ref="DrugDialog"></DrugDialog>
  </div>
</template>

<script>
import DrugDialog from "./DrugDialog.vue"; 
import { mapGetters } from "vuex";
export default {
  name: "drugList",
  data() {
    return {
      // dialogFormVisible:false,
      sale_Status: "",
      page: 1,
      pageSize: 4,
      drugsName: "",
    };
  },
  components: {
    DrugDialog
  },
  computed: {
    ...mapGetters(["drugData"]),
    records() {
      return this.drugData.records;
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    // 
    handleClick(row) {
      this.$refs.DrugDialog.openDialog(row)
    },
    dialogFormVisible() {},

    // 
    toAddDrug() {
      this.$router.push("/addDrug");
    },
    handleSelectionChange() {},
    async handleCurrentChange(val) {
      try {
        const params = {
          page: val,
          pageSize: this.pageSize,
          drugsName: this.drugsName,
        };
        await this.$store.dispatch("showAllDrugs", params);
      } catch (error) {}
    },
    handleSizeChange() {
      // 
    },

    //
    async getData() {
      try {
        const params = {
          page: this.page,
          pageSize: this.pageSize,
          drugsName: this.drugsName,
        };
        await this.$store.dispatch("showAllDrugs", params);
      } catch (error) {}
    },

    //
    async handleDelete(index, row) {
      //(index+1)
      try {
        await this.$store.dispatch("deleteOneDrug", row.drugsId);
        this.$message({
          message: "",
          type: "success",
          showClose: true,
        });
        if ((this.drugData.total - 1) % this.pageSize == 0) {
          const params = {
            page: (this.drugData.total - 1) / this.pageSize,
            pageSize: this.pageSize,
            drugsName: this.drugsName,
          };
          await this.$store.dispatch("showAllDrugs", params);
        } else {
          this.getData();
        }
      } catch (error) {}
    },

    // 
    toUpdatePage(drugsId) {
      // id
      this.$router.push({ path: `/updateDrug/${drugsId}` });
    },

    // 
    async toCheck(drugsId) {
      try {
        const params = {
          drugsId: drugsId,
        };
        await this.$store.dispatch("checkDrug", params);
        this.$message({
          message: "",
          type: "success",
          showClose: true,
        });
        this.getData();
      } catch (error) {}
    },

    // 
    async changeStatus(row) {
      try {
        if (row.saleStatus == 0) {
          row.saleStatus = 1;
        } else if (row.saleStatus == 1) {
          row.saleStatus = 0;
        }
        const status = row.saleStatus;
        const drugsId = row.drugsId;

        await this.$store.dispatch("changeSaleStatus", { status, drugsId });
        this.$router.replace({
          path: "/drugList" + "/",
          query: { status, drugsId },
        });
        this.getData();
      } catch (error) {}
    },
  },
};
</script>

<style lang="less" scoped>
.drugContainer {
  /* display: flex; */
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  position: relative;
  .topOption {
    /* display: flex; */
    width: 660px;
    height: 40px;
    position: absolute;
    left: 50%;
    margin-top: 70px;
    transform: translate(-50%, 50%);
  }
  .drugTable {
    position: absolute;
    /* display: flex; */
    top: 175px;
    width: 1001px;
    left: 50%;
    transform: translate(-50%);
  }
  .buttonstyle {
    width: 79px;
  }
  .pagination {
    position: absolute;
    left: 50%;
    margin-top: 15px;
    transform: translateX(-50%);
  }
}
</style>