<template>
  <div>
    <el-dialog
      :visible.sync="dialogFormVisible"
      :modal-append-to-body="false"
      width="50%"
    >
      <el-form
        :model="DrugDetailForm"
        label-width="100px"
        :inline="true"
        size="mini"
      >

        <el-form-item :label="'\xa0\xa0'">
          <el-input v-model="DrugDetailForm.drugsName"></el-input>
        </el-form-item>

        <el-form-item :label="'\xa0\xa0\xa0\xa0\xa0\xa0'">
          <el-input v-model="DrugDetailForm.drugsSpecifications"></el-input>
        </el-form-item>

        <el-form-item :label="'\xa0\xa0\xa0\xa0\xa0\xa0'">
          <el-input v-model="DrugDetailForm.unit"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="DrugDetailForm.drugsManufactor"></el-input>
        </el-form-item>

        <el-form-item :label="''">
          <el-col :span="11">
            <el-date-picker
              v-model="DrugDetailForm.manufactureTime"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
              style="width: 185px"
            ></el-date-picker>
          </el-col>
        </el-form-item>

        <el-form-item label="">
          <el-col :span="11">
            <el-date-picker
              v-model="DrugDetailForm.expireTime"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
              style="width: 185px"
            ></el-date-picker>
          </el-col>
        </el-form-item>

        <el-form-item label="">
          <el-col :span="11">
            <el-date-picker
              v-model="DrugDetailForm.createTime"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
              style="width: 185px"
            ></el-date-picker>
          </el-col>
        </el-form-item>

        <el-form-item label="">
          <el-col :span="11">
            <el-date-picker
              v-model="DrugDetailForm.checkStartTime"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
              style="width: 185px"
            ></el-date-picker>
          </el-col>
        </el-form-item>

        <el-form-item :label="'\xa0\xa0\xa0\xa0\xa0\xa0'">
          <el-input
            type="number"
            v-model="DrugDetailForm.purchasePrice"
          ></el-input>
        </el-form-item>

        <el-form-item :label="'\xa0\xa0\xa0'">
          <el-input type="number" v-model="DrugDetailForm.salePrice"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-radio-group>
            <el-input
              v-show="DrugDetailForm.saleStatus == 0"
              value=""
            ></el-input>
            <el-input
              v-show="DrugDetailForm.saleStatus == 1"
              value=""
            ></el-input>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="">
          <el-radio-group>
            <el-input
               v-show="DrugDetailForm.isImport == 0"
               value=""
            ></el-input>
            <el-input
               v-show="DrugDetailForm.isImport == 1"
               value=""
            ></el-input>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="" >
          <el-radio-group>
            <el-input v-show="DrugDetailForm.checkStatus == 0"
            value=""
            ></el-input>
            <el-input v-show="DrugDetailForm.checkStatus == 1"
            value=""
            ></el-input>
            <el-input v-show="DrugDetailForm.checkStatus == 2"
            value=""
            ></el-input>
            <el-input v-show="DrugDetailForm.checkStatus == 3"
            value=""
            ></el-input>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="">
          <el-input
            type="textarea"
            style="width: 105%"
            v-model="DrugDetailForm.drugsDesc"
          ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input
            type="textarea"
            style="width: 272%"
            v-model="DrugDetailForm.checkContent"
          ></el-input>
        </el-form-item>

      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      DrugDetailForm: "",
      dialogFormVisible: false,
      // isModal:false
    };
  },
  methods: {
    openDialog(data) {
      this.dialogFormVisible = true;
      this.DrugDetailForm = data;
    },
  },
};
</script>

