<template>
  <div class="fileContainer">
    <div class="topOption">
      <el-button
        type="primary"
        plain
        style="width: 100px; height: 35px; font-size: 10px; text-align: center"
        @click="toAddFile"
        >+</el-button
      >

      <el-input
        placeholder=""
        prefix-icon="el-icon-search"
        style="width: 280px; margin-left: 100px"
        v-model="fileName"
      >
      </el-input>
      <el-button
        type="success"
        style="width: 65px; font-size: 10px; text-align: center"
        @click="getData"
        ></el-button
      >
    </div>
    <div class="fileTable">
      <el-table
        :data="fileData.records"
        stripe
        border
        style="width: 100%; color: black"
      >
        <el-table-column prop="fileId" label="Id" width="100" align="center">
        </el-table-column>
        <el-table-column
          prop="fileName"
          label=""
          width="150"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="filePath"
          label=""
          width="280"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="fileSize"
          label=""
          width="100"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="createTime"
          label=""
          width="180"
          align="center"
        >
        </el-table-column>
        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="toDownload(scope.row)"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <!-- <span class="demonstration"></span> -->
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="page"
          :page-size="pageSize"
          layout="prev, pager, next, jumper"
          :total="fileData.total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>
  
  <script>
import axios from 'axios';
import { mapGetters } from "vuex";
export default {
  name: "fileList",
  data() {
    return {
      page: 1,
      pageSize: 4,
      fileName: "",
    };
  },
  mounted() {
    this.getData();
  },
  computed: {
    ...mapGetters(["fileData"]),

    // deptpageDatarecords
    records() {
      return this.fileData.records;
    },
  },
  methods: {
    // 
    toAddFile() {
      this.$router.push("/addFile");
    },

    async handleCurrentChange(val) {
      try {
        const params = {
          page: val,
          pageSize: this.pageSize,
        };
        await this.$store.dispatch("QueryFile", params);
      } catch (error) {}
    },

    // store,
    // 
    async getData() {
      try {
        const params = {
          page: this.page,
          pageSize: this.pageSize,
          fileName: this.fileName,
        };
        await this.$store.dispatch("QueryFile", params);
      } catch (error) {
        alert(error.msg);
      }
    },

    // 
    async handleDelete(index, row) {
      try {
        const id = row.fileId;
        await this.$store.dispatch("deleteFile", id);
        if ((this.fileData.total - 1) % this.pageSize == 0) {
          const params = {
            page: (this.fileData.total - 1) / this.pageSize,
            pageSize: this.pageSize,
            fileName: this.fileName
          };
          await this.$store.dispatch("QueryFile", params);
        } else {
          this.getData();
        }
      } catch (error) {
        alert(error.msg);
      }
    },

    // 
    async toDownload(row){
      const fileId = row.fileId
      const thisImage =  await  this.$store.dispatch('downLoad',fileId)
      // blob
      // console.log(thisImage)
      // url
      const thisUlrl = URL.createObjectURL(thisImage)
      // console.log(thisUlrl)

      // a
      const link = document.createElement('a');
      // 
      link.download = row.filePath;
      // urlthisUrl
      link.href = thisUlrl;
      // 
      // link.target = '_blank'
      // link
      link.style.display = 'none'
      // linkbody
      document.body.appendChild(link);
      // 
      link.click();
      // url
      URL.revokeObjectURL(link.href);
      // link
      document.body.removeChild(link);
    },

  },
};
</script>
  
  <style lang="less" scoped>
.fileContainer {
  /* display: flex; */
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  position: fixed;
  .topOption {
    /* display: flex; */
    width: 660px;
    height: 40px;
    position: absolute;
    left: 50%;
    margin-top: 70px;
    transform: translate(-50%, 50%);
  }
  .fileTable {
    position: absolute;
    /* display: flex; */
    top: 175px;
    width: 992px;
    left: 50%;
    transform: translate(-50%);
  }
  .pagination {
    position: absolute;
    left: 50%;
    margin-top: 15px;
    transform: translateX(-50%);
  }
}
</style>