<template>
  <div class="deptContainer">
    <div class="topOption">
      <el-button
        type="primary"
        plain
        style="width: 100px; height: 35px; font-size: 10px; text-align: center"
        @click="toAddDept"
        >+</el-button
      >
      <el-button
        type="danger"
        plain
        style="width: 100px; height: 35px; font-size: 10px; text-align: center"
        @click="deleteSomeDepts"
        ></el-button
      >
      <el-input
        placeholder=""
        prefix-icon="el-icon-search"
        style="width: 280px; margin-left: 100px"
        v-model="deptName"
      >
      </el-input>
      <el-button
        type="success"
        style="width: 65px; font-size: 10px; text-align: center"
        @click="getData"
        ></el-button
      >
    </div>
    <div class="deptTable">
      <el-table
        :data="pageData.records"
        stripe
        border
        @selection-change="handleSelectionChange"
        style="width: 100%; color: black"
      >
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column prop="deptId" label="" width="100">
        </el-table-column>
        <el-table-column prop="deptName" label="" width="180">
        </el-table-column>
        <el-table-column prop="telephone" label="" width="180">
        </el-table-column>
        <el-table-column prop="location" label="" width="180">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="mini" @click="toUpdatePage(scope.row.deptId)"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <!-- <span class="demonstration"></span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="page"
          :page-size="pageSize"
          layout="prev, pager, next, jumper"
          :total="pageData.total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "dept",
  data() {
    return {
      page: 1,
      pageSize: 4,
      // deptName
      deptName: "",
      // 
      multipleSelection: [],
    };
  },
  mounted() {
    this.getData();
  },
  computed: {
    ...mapGetters(["pageData"]),
    ...mapGetters(["deptData"]),

    // deptpageDatarecords
    records() {
      return this.pageData.records;
    },
  },
  methods: {
    // 
    toAddDept() {
      this.$router.push("/addDept");
    },
    handleSizeChange(val) {
      // 
      // console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      const params = {
        page: val,
        pageSize: this.pageSize,
      };
      this.$store.dispatch("showPageDepts", params);
    },

    // store,
    // 
    async getData() {
      // this.$store.dispatch("showAllDepts");
      try {
        const params = {
          page: this.page,
          pageSize: this.pageSize,
          // deptName
          deptName: this.deptName,
        };
        await this.$store.dispatch("showPageDepts", params);
      } catch (error) {
        alert(error.msg);
      }
    },

    async handleDelete(index, row) {
      try {
        const id = row.deptId;
        await this.$store.dispatch("deleteDept", id);
        if ((this.pageData.total - 1) % this.pageSize == 0) {
          const params = {
            page: (this.pageData.total - 1) / this.pageSize,
            pageSize: this.pageSize,
            // deptName
            deptName: this.deptName,
          };
          await this.$store.dispatch("showPageDepts", params);
        }else{
          this.getData();
        }
      } catch (error) {
        alert(error.msg);
      }
    },
    // ,
    handleSelectionChange(val) {
      //valvalid
      // console.log(val);
      // valmultipleSection
      this.multipleSelection = val;
    },

    // 
    deleteSomeDepts() {
      try {
        this.multipleSelection.forEach(async (item) => {
          await this.$store.dispatch("deleteDept", item.deptId);
          this.getData();
        });
      } catch (error) {
        alert(error.msg);
      }
    },

    // 
    toUpdatePage(deptId) {
      // id
      this.$router.push({ path: `/updateDept/${deptId}` });
    },
  },
};
</script>

<style lang="less" scoped>
.deptContainer {
  /* display: flex; */
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  position: fixed;
  .topOption {
    /* display: flex; */
    width: 660px;
    height: 40px;
    position: absolute;
    left: 50%;
    margin-top: 70px;
    transform: translate(-50%, 50%);
  }
  .deptTable {
    position: absolute;
    /* display: flex; */
    top: 175px;
    width: 876px;
    left: 50%;
    transform: translate(-50%);
  }
  .pagination {
    position: absolute;
    left: 50%;
    margin-top: 15px;
    transform: translateX(-50%);
  }
}
</style>