<template>
  <div class="loginPage">
    <div class="loginBack">
      <!-- el -->
      <el-form
        label-width="70px"
        :model="loginForm"
        :rules="rules"
        ref="loginForm"
        class="loginContainer"
      >
        <h1 class="loginTitle"></h1>

        <!--  -->
        <el-form-item label="" prop="userName">
          <el-input
            type="text"
            autocomplete="off"
            v-model="loginForm.userName"
            placeholder=""
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item :label="'\xa0\xa0\xa0'" prop="password">
          <el-input
            type="password"
            autocomplete="off"
            v-model="loginForm.password"
            placeholder=""
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="" class="validateCode" prop="code">
          <div class="captcha-box">
            <el-input
              type="text"
              style="width: 200px"
              autocomplete="off"
              v-model="loginForm.code"
              placeholder=""
            ></el-input>
            <img width="80px" height="40px" :src="captchaUrl" />
          </div>
        </el-form-item>

        <el-form-item id="login-btn-box">
          <el-button class="login-btn1" @click="resetForm('loginForm')"
            ></el-button
          >
          <el-button class="login-btn2" type="primary" @click="submitLogin"
            ></el-button
          >
        </el-form-item>
      </el-form>
      <a style="text-decoration: none; font-size: 14px" @click="toRegist"
        ></a
      >
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { loginApi } from "@/api";
export default {
  name: "login",
  data() {
    return {
      loginForm: {
        userName: "",
        password: "",
        code: "",
      },
      captchaUrl: "",
      rules: {
        // :required
        userName: [
          { required: true, message: "", trigger: "blur" },
          // { validator: validateUsername, trigger: "blur" }, //
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          // { validator: this.validatePassword, trigger: "blur" },
        ],
        code: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  // created() {
  //   // // 
  //   // this.changeCaptcha();
  // },
  methods: {
    // MapMutationsuserId
    ...mapMutations(["SETUSERDATA"]),
    // 
    /*   validate
    
    
     promise */
    submitLogin() {
      this.$refs.loginForm.validate(async (valid) => {
        if (valid) {
          // 
          try {
            const data = {
              userName: this.loginForm.userName,
              password: this.loginForm.password,
              // code:this.loginForm.code
            };
           await this.$store.dispatch("userLogin", data);
            this.$router.push("/");
          } catch (error) {}
        }
      });
    },

    // // ()
    // async changeCaptcha() {
    //   let res = await getChangeCaptcha();
    //   if (res.flag == true) {
    //     // const reader = new FileReader();
    //     // // const imageUrl = reader.result;
    //     this.captchaUrl = "data:image/png;base64"+res.data;
    //   }
    // },

    loginSuccess(userName) {
      // 
      // 
      localStorage.setItem("userName", userName);
    },

    // 
    resetForm() {
      this.$refs.loginForm.resetFields();
    },

    // 
    toRegist() {
      this.$router.push("/regist");
    },
  },
};
</script>

<style>
.loginPage {
  width: 100%;
  height: 100%;
  /* background-size: contain; */
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../../assets/ai.png);

  /* background: url(../../assets/loginBack.png); */
  position: relative;

  .loginBack {
    width: 350px;
    border-radius: 15px;
    background-clip: border-box;
    background-color: aliceblue;
    border: 1px solid #eaeaea;
    margin: auto;
    padding: 15px 30px;
    box-shadow: 0 0 25px #a9b2b6;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .loginTitle {
      text-align: center;
      margin: 5px 5px 15px 5px;
    }
    /* ::v-deep #login-btn-box.el-form-item__content {
    margin-left: 100px ;
  } */

    #login-btn-box {
      display: flex;
      margin-left: 25px;
      .login-btn1 {
        margin-right: 20px;
      }
    }
  }
  .captcha-box {
    display: flex;
  }
}
</style>