<template>
  <div>
    <el-dialog
      :visible.sync="dialogFormVisible"
      :modal-append-to-body="false"
      width="25%"
      style="align-items: center"
    >
      <el-form :model="PatientDownLoadForm" label-width="100px" size="small">
        <el-form-item :label="'\xa0\xa0'">
          <el-input v-model="PatientDownLoadForm.patientName"></el-input>
        </el-form-item>

        <el-form-item :label="''">
          <el-select
            placeholder=""
            style="width: 200px"
            v-model="PatientDownLoadForm.deptId"
          >
            <el-option
              v-for="(dept, index) in overCount"
              :key="dept.deptId"
              :label="dept.deptName"
              :value="dept.deptId"
              >{{ dept.deptName }}</el-option
            >
          </el-select>
        </el-form-item>
       
          <el-button
            class="finishUpdate"
            type="primary"
            size="small"
            @click="download"
            ></el-button
          >

      </el-form>
    </el-dialog>
  </div>
</template>
  
  <script>
import { mapGetters } from "vuex";
export default {
  name: "",
  data() {
    return {
      PatientDownLoadForm: "",
      dialogFormVisible: false,
      // isModal:false
    };
  },
  computed: {
    ...mapGetters(["overCount"]),
  },
  methods: {
    openDownLoadDialog(data) {
      this.dialogFormVisible = true;
      this.PatientDownLoadForm = data;
      this.showPatientOver();
    },

    //   
    async showPatientOver() {
      await this.$store.dispatch(
        "queryOverCount",
        this.PatientDownLoadForm.patientId
      );
    },

    // 
    async download() {
      try {
     
        const patientId = this.PatientDownLoadForm.patientId;
        const deptId = this.PatientDownLoadForm.deptId;
        const pdf = await this.$store.dispatch("downLoadPatient", {
          patientId: patientId,
          deptId: deptId,
        });
        console.log(pdf);
        // blob
        //url
        const loadUrl = URL.createObjectURL(pdf);
        console.log(loadUrl);
        // a
        const link = document.createElement("a");
        // 
        link.download =patientId + ''+deptId +''+ '.pdf' ;
        // urlloadUrl
        link.href = loadUrl;
        link.type = 'application/pdf';
        // 
        // link.target = '_blank'
        // link
        link.style.display = "none";
        // linkbody
        document.body.appendChild(link);
        // 
        link.click();
        // url
        URL.revokeObjectURL(link.href);
        // link
        document.body.removeChild(link);
      } catch (error) {}
    },
  },
};
</script>
  
  <style lang="less" scoped>
.el-input {
  width: 200px;
}
.finishUpdate {
  margin-left: 50%;
  transform: translateX(-50%);
}
</style>